<template>
  <div class="info-item">
    <button class="info-item__close" @click="$emit('close')">
      <i class="fal fa-times"></i>
    </button>
    <Transition name="info-item" mode="out-in">
      <div class="info-item__block" v-if="current">
        <img
          :src="imgUrlFull + current.backdrop_path"
          alt=""
          class="info-item__img"
        />
        <div class="info-item__content">
          <h2 class="info-item__content-title">
            {{ current.name || current.title }}
          </h2>
          <p class="info-item__content-overview">{{ current.overview }}</p>
          <p class="info-item__content-data">
            <span
              >{{
                new Date(
                  current.release_date || current.first_air_date
                ).getFullYear()
              }},
            </span>
            <span
              v-for="(genres, idx) in current.genres.slice(0, 3)"
              :key="genres.id"
            >
              {{ genres.name
              }}<span v-if="idx < current.genres.slice(0, 3).length - 1"
                >,
              </span>
            </span>
            <span class="info-item__content-data_runtime">
              {{ current.runtime ? runtime : "" }}
            </span>
          </p>
          <Actors :type="type" :id="current.id" :count="4"/>
          <MoreBtn />
        </div>
      </div>
    </Transition>
  </div>
</template>

<script setup>
import MoreBtn from "@/components/UI/MoreBtn.vue";
import Actors from "@/components/UI/Actors.vue";
import { computed } from "vue";
import { imgUrlFull } from "@/static";
const props = defineProps({
  current: Object,
  type: String,
});

const runtime = computed(() => {
  let min = props.current.runtime % 60;
  let hour = Math.floor(props.current.runtime / 60);

  return hour ? `${hour} ${min}` : `${min}`;
});
</script>
