<template>
  <div class="actors">
    <div class="actor" v-for="actor in actorsList" :key="actor.id">
      <img v-lazy="imgUrl + actor.profile_path" alt="" />
      <span>{{ actor.name }}</span>
    </div>
  </div>
</template>

<script setup>
import { useActors } from "@/stores/actors";
import { computed, onMounted } from "vue";
import { imgUrl } from "@/static";

const props = defineProps(["type", "id", "count"]);
const actorStore = useActors();
const actorsList = computed(() =>
  props.type == "movie" ? actorStore.actorsMovie : actorStore.actorsTv
);

onMounted(() => {
  actorStore.getActors(props.type, props.id, props.count);
});
</script>

<style lang="scss">
.actors {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
}

.actor {
  width: max-content;
  padding: 4px 15px 4px 4px;
  border-radius: 25px;
  border: 1px solid #fff;
  background: rgba(20, 20, 20, 0.5);
  display: flex;
  align-items: center;
  column-gap: 15px;

  & img {
    width: 55px;
    height: 55px;
    border-radius: 50%;
    object-fit: cover;
  }

  & span {
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
  }
}
</style>
