<template>
  <section class="popular" v-if="content">
    <router-link class="popular__title" to="/">
      <span>{{ type == "movie" ? "" : "" }}</span>
      <i class="far fa-chevron-right"></i>
    </router-link>
    <swiper
      :slidesPerView="5"
      :spaceBetween="25"
      :navigation="true"
      :modules="modules"
      class="popular__swiper"
    >
      <swiper-slide
        class="popular__swiper-item"
        v-for="item in content"
        :key="item.id"
        @click="getItem(item.id)"
      >
        <img v-lazy="imgUrl + item.poster_path" alt="" />
      </swiper-slide>
      <swiper-slide>
        <router-link class="popular__swiper-item" :to="`/${type}`">
          <i class="fal fa-chevron-right"></i>
          <span>{{ type == "movie" ? " " : " " }}</span>
        </router-link>
      </swiper-slide>
    </swiper>
    <div class="info" :class="{ active: open }" ref="info">
      <InfoBlock :current="current" :type="type" @close="closeItem" />
    </div>
  </section>
  <Loader v-else />
</template>

<script setup>
import Loader from "@/components/UI/Loader.vue";
import InfoBlock from "@/components/InfoBlock/InfoBlock.vue";
import { Swiper, SwiperSlide } from "swiper/vue";
import { Navigation } from "swiper";
import { ref, onMounted, computed } from "vue";
import { usePopular } from "@/stores/popular";
import { useInfo } from "@/stores/infoId";
import { imgUrl } from "@/static";

import "swiper/scss";
import "swiper/scss/navigation";

const modules = ref([Navigation]);
const props = defineProps({ type: String });
const popular = usePopular();
const infoId = useInfo();
const content = computed(() =>
  props.type == "movie" ? popular.popularMovie : popular.popularTv
);

let open = ref(false);
let current = ref(null);
let info = ref(null);

const getItem = async (id) => {
  current.value = null;
  open.value = true;
  await infoId.getInfo(props.type, id);
  current.value = props.type == "movie" ? infoId.movie : infoId.tv;
  let infoTop = info.value.offsetTop;
  window.scrollTo({
    top: infoTop - headerNav.offsetHeight,
    behavior: "smooth",
  });
};

const closeItem = () => {
  current.value = null;
  open.value = false;
  props.type == "movie" ? (infoId.movie = null) : (infoId.tv = null);
};

onMounted(() => {
  popular.getPopular(props.type);
});
</script>
