import { defineStore } from "pinia";
import axios from "axios";
import { baseUrl, lang, headers } from "@/static";

export const usePopular = defineStore("popular", {
  state: () => ({
    popularMovie: null,
    popularTv: null,
  }),
  actions: {
    async getPopular(type, page = 1) {
      try {
        const res = await axios({
          method: "GET",
          url: `${baseUrl}${type}/popular?language=${lang}&page=${page}`,
          headers: headers,
        });
        if (type == "movie") this.popularMovie = res.data.results;
        else this.popularTv = res.data.results;
      } catch (error) {
        console.error("error in Popular Store", error);
      }
    },
  },
});
