<template>
  <header class="header">
    <nav class="navbar" :class="{ scrollBg: scrollNav > 50 }" id="headerNav">
      <div class="container">
        <router-link class="navbar__logo" to="/">
          <img src="@/assets/images/Logo.svg" alt="" />
        </router-link>
        <button class="navbar__btn" @click="burgerOn">
          <i class="fal fa-bars"></i>
        </button>
        <ul class="navbar__list" :class="{ active: burger }">
          <li @click="burgerOff">
            <button class="navbar__btn">
              <i class="fal fa-times"></i>
            </button>
          </li>
          <li v-for="link in links" :key="link.id" @click="burgerOff">
            <router-link class="navbar__link" :to="link.path">{{
              link.title
            }}</router-link>
          </li>
          <li @click="burgerOff">
            <router-link class="navbar__link" to="/search">
              <i class="fal fa-search"></i>
            </router-link>
          </li>
        </ul>
      </div>
    </nav>
  </header>
</template>

<script setup>
import { ref, reactive, onMounted } from "vue";

const burger = ref(false);
const scrollNav = ref(0);

const links = reactive([
  { id: 1, title: "", path: "/" },
  { id: 2, title: "", path: "/movie" },
  { id: 3, title: "", path: "/tv" },
]);

const burgerOn = () => {
  burger.value = true;
  document.body.style.overflow = "hidden";
};
const burgerOff = () => {
  burger.value = false;
  document.body.style.overflow = "visible";
};

const navbarScrolling = () => {
  scrollNav.value = window.scrollY;
};

onMounted(() => {
  window.addEventListener("scroll", navbarScrolling);
});
</script>
