import { defineStore } from "pinia";
import axios from "axios";
import { baseUrl, lang, headers } from "@/static";

export const useUpcoming = defineStore("upcoming", {
  state: () => ({
    upcoming: null,
  }),
  actions: {
    async getUpcoming(page = 1) {
      try {
        const res = await axios({
          method: "GET",
          url: `${baseUrl}movie/upcoming?language=${lang}&page=${page}`,
          headers: headers,
        });

        const filterResult = res.data.results.filter(
          (item) => item.backdrop_path != null && item.overview != ""
        );

        this.upcoming = filterResult;
      } catch (error) {
        console.error("error in Upcoming Store", error);
      }
    },
  },
});
