import { defineStore } from "pinia";
import axios from "axios";
import { baseUrl, lang, headers } from "@/static";

export const useInfo = defineStore("infoId", {
  state: () => ({
    movie: null,
    tv: null,
  }),
  actions: {
    async getInfo(type, id) {
      try {
        const res = await axios({
          method: "GET",
          url: `${baseUrl}${type}/${id}?language=${lang}`,
          headers: headers,
        });

        if (type == "movie") this.movie = res.data;
        else this.tv = res.data;
      } catch (error) {
        console.error("error in InfoID Store", error);
      }
    },
  },
});
